import * as React from 'react';
import { useNavigate, useParams, Link } from "react-router-dom";
import { useContext, useState, useEffect } from 'react';

import noteContext from './NoteContext';


import API from "./API";


import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

import Snackbar from '@mui/material/Snackbar'; import Autocomplete from '@mui/material/Autocomplete';
import Form from 'react-bootstrap/Form';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import DoneAllIcon from '@mui/icons-material/DoneAll';

export default function MasterHomework() {




     const { school_class } = useParams();


    const [message, setmessage] = useState("");
 
    const [masterSubjects, setMasterSubjects] = useState([]);
    const [masterHomeworks, setmasterHomeworks] = useState([]);



    const [homework, setHomework] = useState("");

    const [subject, setSubject] = useState("");

 

    const [open, setOpen] = useState(false);


    const data = useContext(noteContext);

 



    const navigate = useNavigate();

    let token = data.token;



    const handlesubmit = async () => {


        var reqdata = {

            "school_class": school_class,
            "homework": homework,
            "subject": subject

        }

        const res = await API.post("admin/master/setMasterHomework", reqdata, {
            headers: {
                'content-type': 'text/json', Authorization: 'Bearer ' + token
            }
        });

        const dt = res.data;
        setmasterHomeworks(dt.homeworks);
        
    

        setmessage(dt.message);


        setOpen(true);

        

    }



    const deletehomework = async (item) => {



        const res = await API.post("admin/master/delete_master_homework", item , {
            headers: {
                'content-type': 'text/json', Authorization: 'Bearer ' + token
            }
        });
        const dt = res.data;
 
        setmasterHomeworks(dt.homeworks);
        
    

        setmessage(dt.message);


        setOpen(true);


    }

  

   




    const fetchSubjects = async (school_class) => {

        try {
            const res = await API.get("admin/master/Subjects/" + school_class , {
                headers: {
                    'content-type': 'text/json', Authorization: 'Bearer ' + token
                }
            });
            setMasterSubjects(res.data);




        }
        catch (error) {

            console.log(error);


        }


    };

    const fetchHomeWorks = async (school_class ) => {

        try {
            const res = await API.get("admin/master/Homeworks/" +school_class , {
                headers: {
                    'content-type': 'text/json', Authorization: 'Bearer ' + token
                }
            });


            setmasterHomeworks(res.data);



        }
        catch (error) {

            console.log(error);


        }


    };

    useEffect(() => {
 


         fetchSubjects( school_class);


        fetchHomeWorks( school_class);



    }, []);

 




    return (

        <>
             

 




            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography>New Homework</Typography>
                </AccordionSummary>
                <AccordionDetails>

                    <div className="mb-3">

                        <Form.Select onChange={(e) => setSubject(e.target.value)} >
                            {masterSubjects.map((e) => <option value={e} key={e}>{e}</option>)}
                        </Form.Select>





                    </div>

                    <div className="mb-3">


                        <Form.Label>HomeWork</Form.Label>


                        <Form.Control onChange={(e) => setHomework(e.target.value)}
                            type="text"

                        />




                    </div>






                    <div className="mb-3">

                        <Button onClick={handlesubmit} size="large" variant="contained">Set Homework</Button>

                        <Snackbar
                            open={open}
                            autoHideDuration={2000}
                            onClose={() => setOpen(false)}
                            message={message}

                        />
                    </div>





                </AccordionDetails>
            </Accordion>



            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel2a-content"
                    id="panel2a-header"
                >
                    <Typography>  Homework</Typography>
                </AccordionSummary>
                <AccordionDetails>


                    <table className="table  table-auto  ">

                        
                        
                        <tbody>

                            {masterHomeworks.map((item, index) => (


                                <tr key={index}>



                                    <td>   {item.subject} </td>

                                    <td>   {item.homework} </td>

                                    <td>   {item.created_at} </td>



                                    <td>   <Button onClick={() => deletehomework(item)} variant="contained"> <DeleteForeverIcon /></Button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>



                </AccordionDetails>
            </Accordion>



        </>

    )
}